import logo_kuta from  './assets/logo_free-file.png';
import React from 'react';
import './Styles/Header.css'

function Header() {
 
  const [isActiveMenu, setIsActiveMenu] = React.useState(false);
  const activeButtomM = () =>{
    setIsActiveMenu(true)
  }
  const activeButtomC = () =>{
    setIsActiveMenu(false)
  }

  window.addEventListener("scroll", function() {
    var header = document.querySelector("header");
    if(header != null ){
      header.classList.toggle('down', this.window.scrollY > 0);
    }
    

  })

    return (
      <header>
        <div className="img_logo">
          <a href="#main-section" ><img src={logo_kuta} alt="Main Logo"/></a>
          <b>Victo Raul Huaycho Quispe</b>
        </div>

        <nav className={`menu${isActiveMenu ? " active" : ""}`}>

          <div className="btn">
            <i className="fas fa-times close-btn" onClick={activeButtomC}></i>
          </div>

          <ul>
            <li><a href="#one-section">Inicio</a></li>
            <li><a href="#two-section">Perfil Profesional</a></li>
            <li><a href="#third-section">Proyectos</a></li>
            <li><a href="#fourth-section">Noticias/Actualidad</a></li>
            <li><a href="#fifth-section">Networking</a></li>
          </ul>

        </nav>

        <div className="btn">
          <i className="fas fa-bars menu-btn" onClick={activeButtomM}></i>
        </div>
      
      </header>

        
    );
  }
  
  export default Header;