import React from 'react';
import './Styles/Profile.css'
import Card from './Card.js';
import Competencia from './assets/competencia.svg'
import Objetivo from './assets/objetivo.svg'
import Logro from './assets/logros.svg'

export default function Profile() {
  
    return (
        <section className="section-two" id="two-section" >
          <h2>Perfil Profesional</h2>
          <hr/>

          <div className="container-profile">

            <Card className="card-pitch" >
              <img src="https://www.epibag.com/wp-content/uploads/2019/11/definition-980x679.png" height="200px" alt="img logo"/>
              <p>Soy Víctor Raul Huaycho Quispe, estudio la carrera de diseño y desarrollo de software, soy una persona muy analítica, y que le gustar obtener muchos conocimientos sobre la tecnología en general.</p>
                <p>Soy capaz de adaptarme a cualquier tipo de proyecto o trabajo relacionado a la tecnología, también puedo analizar los pequeños detalles de todos los proyectos que este desarrollando, por lo que me gusta enfocarme en uno solamente.
              </p>
            </Card>

            <Card className="card-competitions" >
              <img src={Competencia} alt="imagen competencia"/>
              <h3>Mis competencias</h3>
              
              <div className="card-content">
                
                <ul>
                  <li>Responsabilidad</li>
                  <li>Compromiso</li>
                  <li>Trabajo en equipo</li>
                  <li>Comunicacion Eficaz</li>
                  <li>Capacidad de tomar decisiones y dirigir</li>
                  <li>Capacidad analítica</li>
                </ul>
                
              </div>
            </Card>

            <Card className="card-objective" >
              <img src={Objetivo} alt="objetivo imagen"/>
              <h3>Mi Obtevio Profecional</h3>
              <div className="card-content">
                <p>Aspiro poder desarrollarme de forma laboral y poder ayudar a la empresa como mis conocimientos, tambien
                busco aprender como es el desempeño de un entorno laboral en distintos ámbitos.
                Estoy dispuesto a aportar todos los conocimientos técnicos, laborales y sociales que obtuve.
                </p>
              </div>
            </Card>

            <Card className="card-achievements" >
              <img src={Logro} alt="objetivo imagen"/>
              <h3>Logros y Reconocimientos</h3>
              <div className="card-content">
                <ul>
                  <li>Conseguí una beca para cursar una especialización de Mantenimiento de Computadoras en la Universidad Andina Nestor Cacerers. </li>
                  <li>Diseñe una pagina web para un evento de mi Colegio GUE Mariano Melgar, aumentando el interes en este.</li>
                  <li>Consegui el 5 puesto de mejor estudiante al culminar mi Estudio, recibiendo una diploma de reconocimiento.</li>
                </ul>
              </div>
            </Card>

          </div>

        </section>
    )
}
